import { db } from "../firebase/firebase-config";
import { collection, doc, setDoc, query, updateDoc, deleteDoc} from "firebase/firestore";
import { types } from "../types/types";
import { loadNotes } from "../helpers/loadNotes";
import Swal from "sweetalert2";
import { fileUpload } from "../helpers/fileUpload";

export const startNewNote = () => {
    return async(dispatch, getState) => {
        
        const {uid} = getState().auth;

        const newNote = {
            title:'',
            body: '',
            date: new Date().getTime()
        }

        const docRef = query(collection(db, `${ uid }/journal/notes`));
        const docRef2 = doc(docRef);
        await setDoc(docRef2, newNote);
        dispatch(activeNote(docRef2.id, newNote));
        dispatch(addNewNote(docRef2.id, newNote))
    }
}

//getState es como el useSelector, pero el useSelector puede obtener cuando se rerenderiza

export const activeNote = (id,note) => ({
    type: types.notesActive,
    payload: {
        id,
        ...note
    }
})

export const addNewNote = (id,note) => ({
    type: types.notesAddNew,
    payload: {
        id, ...note
    }
})

export const startLoadingNotes = (uid) => {
    return async (dispatch) => {
        const notes = await loadNotes(uid);
        dispatch(setNotes(notes));
    }
}

export const setNotes = (notes) => ({
    type: types.notesLoad,
    payload: notes
})

export const startSaveNote = (note) => {
    return async (dispatch, getState) => {
        const {uid} = getState().auth;

        if(!note.url){
            delete note.url;
        }

        const noteToFirestore = {...note};
        delete noteToFirestore.id;

        const noteRef = doc(db, `${uid}/journal/notes/${note.id}`)
        await updateDoc(noteRef,noteToFirestore);

        dispatch(refreshNote(note.id,note));
        Swal.fire('Saved',note.title,'success'); //mensaje popout
    }
}

export const refreshNote = (id, note) => ({
    type: types.notesUpdate,
    payload: {
        id, 
        note: {
            id,
            ...note
        }
    }
})

export const startUploading = (file) => {
    return async (dispatch, getState) => {
        const {active} = getState().notes

        Swal.fire({
            title: 'Uploading...',
            text: 'Please wait...',
            showConfirmButton: false,
            allowOutsideClick: false,
            willOpen: () => {
                Swal.showLoading();
              }
        })

        const fileUrl = await fileUpload(file);
        active.url = fileUrl;

        dispatch(startSaveNote(active))

        Swal.close();
    }
}

export const startDeleting = (id) => {
    return async (dispatch, getState) => {
        const uid = getState().auth.uid;
        //await db.doc(`${uid}/journal/notes/${id}`).delete();

        const noteDelete = doc(db, `${uid}/journal/notes/${id}`)
        await deleteDoc(noteDelete);

        dispatch(deleteNote(id));

    }
}

export const deleteNote = (id) => ({
    type: types.notesDelete,
    payload: id
})

export const noteLogout = () => ({
    type: types.notesLogoutCleaning
})

//funciones que empiezan con Start son asincronas